* {
  box-sizing: border-box;
}

html {
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  background-color: white;
}

.pageWrap {
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
  min-height: calc(100vh - 60px);
  background-size: 400% 400%;
  max-width: 96%;
  margin: 2%;
  position: fixed;
  width: -webkit-fill-available;
  overflow-y: scroll;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  z-index: -100;
}
#white-top {
  border-top: 33px solid white;
  position: fixed;
  width: 100%;
  top: 0%;
  z-index: 100;
  overflow-y: scroll;
}
#white-bottom {
  border-bottom: 33px solid white;
  position: fixed;
  width: 100%;
  bottom: 0%;
  z-index: 100;
  overflow-y: scroll;
}
#white-left {
  border-left: 20px solid white;
  position: fixed;
  height: 100%;
  z-index: 100;
  overflow-y: scroll;
}
#white-right {
  border-left: 20px solid white;
  position: fixed;
  right:0%;
  height: 100%;
  z-index: 100;
  overflow-y: scroll;
}
::selection {
  background-color: pink;
}
#face-icon {
  -webkit-animation: reveal 1s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s backwards;
          animation: reveal 1s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s backwards;
  object-fit: contain;
  float: right;
  width: 32%;
  top: 13%;
  right: 10%;
  position: absolute;
}
#footer {
  position: absolute;
  bottom: -280%;
  border-top: 3px solid white;
  width: 40%;
}
/*KEYFRAMES */
@-webkit-keyframes reveal {
  from {
    opacity: 0%;
    -webkit-transform: translateY(8px);
            transform: translateY(8px);
  }
  to {
    opacity: 100%;
  }
}
@keyframes reveal {
  from {
    opacity: 0%;
    -webkit-transform: translateY(8px);
            transform: translateY(8px);
  }
  to {
    opacity: 100%;
  }
}
@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

