  /*KEYFRAMES */
  @-webkit-keyframes scrollBob {
    0%,
    10% {
      -webkit-transform: none;
              transform: none;
      opacity: 1;
      color: rgb(222, 202, 70);
    }
    1%,
    8% {
      -webkit-transform: translateY(-2px);
              transform: translateY(-2px);
