@-webkit-keyframes animate-rocket {
  0% {
    -webkit-transform: scale(1) rotate(-10deg);
            transform: scale(1) rotate(-10deg);
  }
  5% {
    -webkit-transform: scale(1.25) rotate(15deg);
            transform: scale(1.25) rotate(15deg);
  }
  10% {
    -webkit-transform: scale(1.5) rotate(-5deg);
            transform: scale(1.5) rotate(-5deg);
  }
  15% {
    -webkit-transform: scale(1.75) rotate(10deg);
            transform: scale(1.75) rotate(10deg);
  }
  20% {
    -webkit-transform: scale(2) rotate(0);
            transform: scale(2) rotate(0);
  }
  100% {
    -webkit-transform: scale(20) translate3D(-100vw, -100vh, 0) rotate(0);
            transform: scale(20) translate3D(-100vw, -100vh, 0) rotate(0);
  }
}

@keyframes animate-rocket {
  0% {
    -webkit-transform: scale(1) rotate(-10deg);
            transform: scale(1) rotate(-10deg);
  }
  5% {
    -webkit-transform: scale(1.25) rotate(15deg);
            transform: scale(1.25) rotate(15deg);
  }
  10% {
    -webkit-transform: scale(1.5) rotate(-5deg);
            transform: scale(1.5) rotate(-5deg);
  }
  15% {
    -webkit-transform: scale(1.75) rotate(10deg);
            transform: scale(1.75) rotate(10deg);
  }
  20% {
    -webkit-transform: scale(2) rotate(0);
            transform: scale(2) rotate(0);
  }
  100% {
    -webkit-transform: scale(20) translate3D(-100vw, -100vh, 0) rotate(0);
            transform: scale(20) translate3D(-100vw, -100vh, 0) rotate(0);
  }
}

